body {
  background-color: #501b1d;
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  font-size: 1em;
  font-weight: 300;
}


.loading-container {
  width: 100%;
  text-align: center;
  margin: 30px auto;
}

.error {
  width: 100%;
  margin: 30px 0;
  text-align: center;
  color: #ff335f;
}

/* Remove close icon from input on IE */
input::-ms-clear {
  display: none;
}
.Header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #2E1114;
    width: 100%;
    height: 80px;
  }

  .Header-button {
    position: absolute;
    background-color:#adadad;
    color:#2E1114;
    text-align: center;
    text-decoration:none;
    display: inline-block;
    border-radius:8px;
    top: 30px;
    left: 20px;
    padding: 5px 10px;
  }
  .Header-button:hover{
      background-color:white;
      cursor:pointer; /* change arrow to hand */
  }
  
  @media (max-width: 200px) {
    .Header-button {
      display: none;
    }
  }
.tb-container {
  overflow-x: auto; /* Needed for table to be responsive */
}

.tb {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

.tb-head {
  background-color: #501B1D,;
}

.tb-head tr th {
  padding: 10px 20px;
  color: #ADADAD;
  text-align: left;
  font-size: 1 rem;
  font-weight: 400;
}

.tb-body {
  text-align: left;
  /* background-color: #64485C; */
  background-color:#ADADAD;
}

.tb-body tr td {
  padding: 24px 20px;
  border-bottom: 2px solid #83677B; /* #87939e; */ /*#64485c */
  color: #64485c;
  cursor: pointer;
}

.tb-body tr:hover{
  /* will only change background color when hoving row that belongs to the body */
  background:white;
}

.tb-name {
  color: #64485c;
  /* color: #501b1d; */
  margin-right: 18px;
  font-size: 1.5em;
}

.tb-id {
  color: #64485c,;
  margin-left: 50px;
}


