/*
  Colors used:

  text:               white;    white: #fff
  second-level text:  #a5bace;  gray: #adadad

  error-red:          #ff335f;

  green:              #33f944;

   lighter burgandy/purple?:     #64485c
   normal burgandy:      #501b1d
   very dark burgandy:   #2E1114;

  another light burgandy: #83677b;
  another dark blue:  #3e6fa0;
*/

