body {
  background-color: #501b1d;
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  font-size: 1em;
  font-weight: 300;
}


.loading-container {
  width: 100%;
  text-align: center;
  margin: 30px auto;
}

.error {
  width: 100%;
  margin: 30px 0;
  text-align: center;
  color: #ff335f;
}

/* Remove close icon from input on IE */
input::-ms-clear {
  display: none;
}